import React from 'react';
import { Radio } from 'antd';

const Box = () => {
  return(
    <div className="box box-default">
      <div className="box-header">Basic</div>
      <div className="box-body">
        <Radio>Radio</Radio>
      </div>
    </div>
  )
}

export default Box;


